.bulb-editor-section-label {
  color: #3b3030;
  font-size: 1.3em;
  margin-bottom: 0.5em; }

.bulb-editor-field-label {
  color: #7c7c7c;
  margin-bottom: 0.5em;
  border-bottom: 1px solid #8092af; }

.bulb-editor-answer {
  background-color: #ececec;
  padding: 1rem;
  margin: 1rem 0;
  display: grid;
  grid-template-columns: 1fr auto;
  grid-column-gap: 1rem;
  justify-items: stretch;
  align-items: stretch; }
  .bulb-editor-answer-label {
    font-weight: bold; }
    .bulb-editor-answer-label-feedback {
      font-weight: bold;
      font-size: 0.9em; }
