.bulb-video {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  max-width: 90%;
  margin: 0 auto; }

.bulb-video-caption {
  display: flex;
  margin-bottom: 1em;
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif; }

.bulb-video-caption:hover {
  cursor: pointer; }

.bulb-video-player {
  background: gray;
  grid-column: span 3; }

.bulb-video-captions-container {
  padding: 30px;
  min-height: 0;
  height: 0;
  padding-bottom: 161.7%;
  overflow-y: auto; }

.bulb-video-caption-timestamp,
.bulb-video-caption p {
  padding: 0.2em 0.5em;
  margin: 0; }

.bulb-video-caption-timestamp {
  align-self: start;
  background-color: lightblue;
  border-radius: 5px; }

.bulb-video-player {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  height: 0; }

.bulb-video-player iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }
